<template>
  <div class="container">
    <v-card class="mx-auto" width="256" tile>
      <v-navigation-drawer permanent>
        <v-system-bar></v-system-bar>
        <v-list>
          <v-list-item>
            <v-list-item-avatar>
              <v-img src="https://cdn.vuetifyjs.com/images/john.png"></v-img>
            </v-list-item-avatar>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-list-item-title class="title">{{name}}</v-list-item-title>
              <v-list-item-subtitle>{{area}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <v-divider></v-divider>
        <v-list nav dense>
          <v-list-item-group color="primary">
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mail</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>{{email}}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-icon>
                <v-icon>location_on</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>{{city}}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>
                <div class="text-center">
                  <v-btn to="/" rounded color="primary" dark>Atrás</v-btn>
                </div>
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>
                <div class="text-center">
                  <v-btn @click="deleteEmployee" rounded color="red" dark>Eliminar</v-btn>
                </div>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <div class="text-center">
                  <v-btn v-bind:to="{ name: 'edit-employee', params:{employee_id:id} }"  rounded color="success" dark>Editar</v-btn>
                </div>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-navigation-drawer>
    </v-card>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "view-employee",
  data() {
    return {
      id: null,
      name: null,
      area: null,
      email: null,
      city: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(row => {
          next(vm => {
            vm.id = row.data().id;
            vm.name = row.data().name;
            vm.area = row.data().area;
            vm.city = row.data().city;
            vm.email = row.data().email;
          });
        });
      });
  },
  methods: {
    deleteEmployee() {
      console.log(this.$route);
      if (confirm("¿Estás seguro?")) {
        db.collection("employees")
          .where("id", "==", this.$route.params.employee_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(row => {
              row.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>